      p.mt-5(data-aos="fade-up" style="position:relative") Magerit, se presenta como una metodologa para la gestin del riesgo, la cual est basada en la norma ISO 31000, precisamente en su apartado 4.4 denominado Implementar la Gestin del Riesgo, lo que la consolida como un marco para su gestin, como lo presenta la misma metodologa en la figura No 19.

      .col-lg-10.m-auto.mt-5(style="position:relative")
        .titulo-sexto.color-acento-contenido.mb-0(style="position:relative")
          h5 Figura 19.
          span Marco de trabajo para la gestin de riesgos de acuerdo a ISO 31000.
        figure
          img.d-none.d-lg-block(src="@/assets/curso/tema3/3a.svg" data-aos="zoom-in-up" alt="La imagen presenta la metodologa Magerit para la gestin del riesgo, la cual compienza con los principios, luego el lmandato y compromiso, pasa a la implementacin de la gestin de riesgos, luego viene el seguimiento y revisin del marco y finalmente la mejora continua del marco, para regresar al diseo del marco de trabajo.")
          img.d-block.d-lg-none(src="@/assets/curso/tema3/3c.svg" data-aos="zoom-in-up" alt="La imagen presenta la metodologa Magerit para la gestin del riesgo, la cual compienza con los principios, luego el lmandato y compromiso, pasa a la implementacin de la gestin de riesgos, luego viene el seguimiento y revisin del marco y finalmente la mejora continua del marco, para regresar al diseo del marco de trabajo.")
