    p.mt-5(data-aos="fade-up") Las propuestas normativas buscan identificar el nivel de vulnerabilidad de los activos de informacin, siendo Magerit preferida por su sencillez y su objetividad en el momento de su aplicacin para la evaluacin del riesgo. A continuacin se exponen los objetivos de MAGERIT.

    .row.mt-5.col-lg-10.m-auto(style="background-color:#D5CEF8;border-top-right-radius:140px;")
