              | Ayuda a las organizaciones con la evaluacin de la seguridad en las mismas.
        .col-auto.pe-0
          figure(style="position:relative")
            img(src="@/assets/curso/tema3/6.svg" data-aos="fade-left")

    .col-lg-10.m-auto.mt-5
      p(data-aos="fade-up") A partir de las siguientes dimensiones de seguridad, se debe realizar la valoracin en cada uno de los activos para proceder con el ejercicio de anlisis de riesgo, as:

    .cont_3_2.mt-5(style="position:relative")
      figure
        .video
          iframe(width="560" height="315" src="https://www.youtube.com/embed/7Omq8brpg5A" title="Magerit (Metodologa de Anlisis y Gestin de Riesgos de los Sistemas de Informacin" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
        figcaption.position-relative Video. Magerit (Metodologa de Anlisis y Gestin de Riesgos de los Sistemas de Informacin)

      .row.mt-5.col-lg-10.m-auto
        .col-lg-8.mb-4.mb-lg-0(style="place-self:center;position:relative")
          p(data-aos="fade-up") El procedimiento de anlisis de riesgos se puede desarrollar en cualquier tipo de organizacin que desarrolle sus funciones apoyado en sistemas de informacin y comunicaciones, independiente del sector bien sea pblico o privado, y se recomienda realizarlo con mayor razn cuando se manejan datos confidenciales.
          p.mt-4(data-aos="fade-up") Los pasos generales para desarrollar un anlisis de riesgos, relacionados con la seguridad informtica, se evidencian en el siguiente video titulado Procedimiento de Anlisis de Riesgos - Pasos:
        .col-lg-4(style="place-self:center;position:relative")
