    .titulo-segundo
      h2#t_3_1 3.1  Identificacin de activos

    p(data-aos="fade-up") Se debe tener en cuenta que se deben identificar los activos relacionados con los procesos de negocio, que se deben proteger, teniendo en cuenta los elementos de un sistema de informacin.
    p.mt-4(data-aos="fade-up") La identificacin permite clasificar los activos a los que se les debe brindar mayor proteccin, as:

    .col-lg-10.m-auto.mt-5
      figure
        img.d-none.d-lg-block(src="@/assets/curso/tema3/9.svg" data-aos="zoom-in-up")
