    p.mt-5(data-aos="fade-up") Tambin se establecen las escalas de medicin cualitativa y cuantitativa, las cuales se exponen a continuacin:

    .row.mt-5
      .col-lg-6.p-0
        .h-100.p-4.pb-5(style="background-color:#36257B;")
          .col-lg-10.m-auto
            .text-center
