            p.text-white.mt-5(data-aos="fade-up") #[strong Valoracin cualitativa:] es un proceso que permite analizar las caractersticas y problemas del fenmeno a evaluar permitiendo identificar rpidamente el peso de cada activo en comparacin a los dems.
      .col-lg-6.p-0
        .h-100.p-4.pb-5(style="background-color:#583AE2;")
          .col-lg-10.m-auto
            .text-center
              figure(style="position:relative")
                img(src="@/assets/curso/tema3/12.png" data-aos="zoom-in-up" style="max-width:180px;display:inline;")
            p.text-white.mt-5(data-aos="fade-up") #[strong Valoracin cuantitativa:] estas valoraciones, se presentan en escalas numricas absolutas las cuales son algo complejas de identificar; pero permiten establecer operaciones matemticas para sus evaluaciones.

    p.mt-5(data-aos="fade-up") Un ltimo elemento a tener presente en la caracterizacin de los activos es el valor de la interrupcin del servicio, debido a que esta valoracin se diferencia de las anteriores porque afecta directamente la disponibilidad, y se requiere determinar el costo de tenerlo por fuera de servicio por un determinado tiempo; esta valoracin se debe establecer en una lnea de tiempo que permita identificar claramente las consecuencias de no contar con un sistema de informacin disponible y su impacto para la organizacin, por lo general se presentan en grficos como el que se presenta en la figura a continuacin:

    .col-lg-10.m-auto.mt-5(style="position:relative")
