        img.d-block.d-lg-none(src="@/assets/curso/tema3/14.svg" data-aos="zoom-in-up" alt="La imagen presenta el coste de interrupcin de la disponibilidad, donde el eje X es representado por el tiempo y el eje Y por el coste.")
      figcaption(style="background-color:#F6F6F6;font-weight:400;position:relative") Nota. https://cutt.ly/KB7x2kX

