    .titulo-segundo
      h2#t_3_2 3.2  Identificacin de amenazas

    p(data-aos="fade-up") La  metodologa Magerit, permite identificar las amenazas de cada uno de los activos de informacin en la organizacin, para lo cual, nos sugiere una serie de amenazas tpicas, que mostramos a continuacin:

    .tarjeta.p-5.mt-5.cont_slyder1
      SlyderA(tipo="b")
        .row
          .col-lg-6.mb-4.mb-lg-0(style="place-self:center")
            h5(style="color:#36257B;") Natural
            p Todos los eventos que origina la naturaleza y puede afectar un activo de informacin, como desastres naturales, incendios accidentales, tormentas, temperaturas extremas, terremotos e inundaciones, y amenazas ocasionadas por el hombre.
          .col-lg-6(style="place-self:center")
            figure
              img(src='@/assets/curso/tema3/16.png')
        .row
          .col-lg-6.mb-4.mb-lg-0(style="place-self:center")
            h5(style="color:#36257B;") Industrial
            p Generados por efectos resultantes o asociadas a eventos industriales; cualquier empresa con operaciones industriales digitalizadas es susceptible de ser atacada, las ms afectadas son las proveedoras de electricidad, agua, petrleo y gas, las alimentarias y las farmacuticas.
          .col-lg-6(style="place-self:center")
            figure
              img(src='@/assets/curso/tema3/17.png')
        .row
          .col-lg-6.mb-4.mb-lg-0(style="place-self:center")
            h5(style="color:#36257B;") Errores en las aplicaciones
            p Vulnerabilidades tcnicas en programas, normalmente se presentan por errores de cdigo, fallas en el diseo de #[em software], problemas en la implementacin o malas prcticas de configuracin de activos.
          .col-lg-6(style="place-self:center")
            figure
              img(src='@/assets/curso/tema3/18.png')
        .row
          .col-lg-6.mb-4.mb-lg-0(style="place-self:center")
            h5(style="color:#36257B;") Causados de forma accidental
            p Originados por personas generalmente por error u omisin, pueden ser errores sistemticos y errores accidentales.
          .col-lg-6(style="place-self:center")
            figure
              img(src='@/assets/curso/tema3/19.png')
        .row
          .col-lg-6.mb-4.mb-lg-0(style="place-self:center")
            h5(style="color:#36257B;") Causadas por las personas de forma deliberada
            p Segn el Artculo 269D: Dao Informtico. El que, sin estar facultado para ello, destruya, dae, borre, deteriore, altere o suprima datos informticos, o un sistema de tratamiento de informacin o sus partes o componentes lgicos causados por terceros, o por personas con intereses propios.
          .col-lg-6(style="place-self:center")
            figure
              img(src='@/assets/curso/tema3/20.png')
        .row
          .col-lg-6.mb-4.mb-lg-0(style="place-self:center")
            h5(style="color:#36257B;") Identificacin y tipificacin de activos
            p Una vez se haya realizado la identificacin de las amenazas, se procede a realizar la valoracin de las mismas, y se requiere identificar cul sera el efecto de influencia sobre el activo.
          .col-lg-6(style="place-self:center")
            figure
              img(src='@/assets/curso/tema3/21.png')
        .row
          .col-lg-6.mb-4.mb-lg-0(style="place-self:center")
            h5(style="color:#36257B;") Degradacin
            p La degradacin mide el dao causado por un incidente en el supuesto de que ocurriera, y esta se suele caracterizar como una fraccin del valor del activo y da origen a expresiones como activo totalmente degradado o degradado en una pequea fraccin.
          .col-lg-6(style="place-self:center")
            figure
              img(src='@/assets/curso/tema3/22.png')
        .row
          .col-lg-6.mb-4.mb-lg-0(style="place-self:center")
            h5(style="color:#36257B;") Probabilidad
            p Qu tan probable es que una amenaza sea aprovechada y se materialice en un riesgo.
          .col-lg-6(style="place-self:center")
            figure
              img(src='@/assets/curso/tema3/23.png')

    .col-lg-10.m-auto.mt-5
      p(data-aos="fade-up") La complejidad de ocurrencia es compleja de determinar y expresar, y se puede apoyar en escalas de tipo nominal, como se muestra en la siguiente tabla:

      .col-lg-8.m-auto.mt-5
        .titulo-sexto.color-acento-contenido(style="position:relative")
