    p.mt-5(data-aos="fade-up") Ahora bien, en trmino de periodo de tiempo, lo ms recomendado es realizarlo en trminos de 1 ao para establecer una frecuencia, de tal manera se puede establecer una escala de probabilidad de ocurrencia como se muestra en la siguiente tabla:

