    .titulo-segundo
      h2#t_3_3 3.3  Determinacin del impacto potencial

    p(data-aos="fade-up") El impacto potencial, se le llama a la medida del dao sobre un activo en particular, a partir de la materializacin de una amenaza, este impacto se puede determinar una vez se haya establecido el valor de los activos y la degradacin que causa dicha amenaza.

    p.mt-4(data-aos="fade-up") Teniendo en cuenta lo anterior sobre la medida del dao se establece el impacto de la siguiente manera:

    .row.mt-5.pt-5.pb-5.pe-5(style="background-color:#E3E5FB;")
      .col-xl-4.col-lg-5.ps-0.d-none.d-lg-block
        figure(style="position:relative")
          img(src="@/assets/curso/tema3/24.png" data-aos="fade-right")
      .col-xl-8.col-lg-7(style="place-self:center;")
        ul.lista-ul
          li.mb-0(data-aos="fade-up")
            i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
            | Se calcula para cada activo, por cada amenaza y en cada dimensin de valoracin.
          li.mb-0(data-aos="fade-up")
            i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
            | El impacto se incrementa, cuando mayor es el valor propio de un activo y mayor se degrade el activo de informacin, as como cuando el activo depende de otros sistemas o viceversa.
          li.mb-0(data-aos="fade-up")
            i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
            | El impacto es mayor de acuerdo a su dependencia del mismo.
          li.mb-0(data-aos="fade-up")
            i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
            | Este impacto, permitir identificar las consecuencias que tendra una afectacin y cmo afectara a la organizacin directamente.
          li.mb-0(data-aos="fade-up")
            i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
            | El impacto potencial, se puede clasificar de la siguiente manera:
        .mt-4.bg-white.p-4
          p(data-aos="fade-up") #[strong Impacto acumulado:]
          ul.lista-ul--color.mt-3
            li.mb-0(data-aos="fade-up")
              i.lista-ul__vineta
              | Se tiene como referencia el valor acumulado.
            li.mb-0(data-aos="fade-up")
              i.lista-ul__vineta
              | Las amenazas a las que se puede enfrentar un activo.
            li.mb-0(data-aos="fade-up")
              i.lista-ul__vineta
              | Puede acumularse sobre activos que no sean dependientes entre s, y no hereden valor de un activo superior.
            li.mb-0(data-aos="fade-up")
              i.lista-ul__vineta
              | No se recomienda agregar el impacto acumulado sobre activos que no sean independientes, dado que supondra ponderar el impacto al incluir varias veces el valor acumulado de activos superiores.
            li.mb-0(data-aos="fade-up")
              i.lista-ul__vineta
              | Pueden concluir el impacto de otras amenazas sobre un mismo activo, aunque se recomienda considerar en qu medida las otras amenazas son independientes y pueden ser concurrentes.
            li.mb-0(data-aos="fade-up")
              i.lista-ul__vineta
              | El impacto de una amenaza se puede agregar en diferentes dimensiones.
          p.mt-4(data-aos="fade-up") #[strong Impacto repercutido:] se puede aplicar sobre activos de diferente tipo y se determina sobre un activo partir de:
          ul.lista-ul--color.mt-3
            li.mb-0(data-aos="fade-up")
              i.lista-ul__vineta
              | La importancia para la organizacin.
            li.mb-0(data-aos="fade-up")
              i.lista-ul__vineta
              | Las amenazas a las que estn expuestas los activos de los que depende.
            li.mb-0(data-aos="fade-up")
              i.lista-ul__vineta
              | El impacto de una amenaza se puede agregar en diferentes dimensiones.

