    .cont_3_3.mt-5(style="position:relative")
      .titulo-segundo(style="position:relative")
        h2#t_3_4 3.4  Determinacin del riesgo potencial
      p(data-aos="fade-up" style="position:relative") El riesgo potencial, se le llama a la medida del dao probable sobre un sistema, teniendo en cuenta el impacto de las amenazas de cada uno de los activos, este riesgo crece con el impacto y la probabilidad, estableciendo las zonas de riesgo como se determinan a continuacin:

      .row.mt-5(style="position:relative")
        .col-lg-6
          .row.m-0.pt-4.pb-4.mb-4.mb-lg-0(style="border-top:1px #36257B solid;border-bottom:1px #36257B solid;")
            .col-auto(style="place-self:center")
              figure
                img(src="@/assets/curso/tema3/26.png" data-aos="fade-right")
            .col(style="place-self:center")
              h5(data-aos="fade-up") Zona 1
              p.mt-3.mb-0(data-aos="fade-up") Zona de riesgo muy probable, as como de alto impacto.
          .row.m-0.pt-4.pb-4.mb-4.mb-lg-0(style="border-bottom:1px #36257B solid;")
            .col-auto(style="place-self:center")
              figure
                img(src="@/assets/curso/tema3/27.png" data-aos="fade-right")
            .col(style="place-self:center")
              h5(data-aos="fade-up") Zona 2
              p.mt-3.mb-0(data-aos="fade-up") Franja amarilla: abarca un amplio espectro que representa desde las diferentes situaciones. improbables y de impacto medio, hasta situaciones muy probables, pero de impacto reducido.
        .col-lg-6
          .row.m-0.pt-4.pb-4.mb-4.mb-lg-0(style="border-top:1px #36257B solid;border-bottom:1px #36257B solid;")
            .col-auto(style="place-self:center")
              figure
                img(src="@/assets/curso/tema3/28.png" data-aos="fade-right")
            .col(style="place-self:center")
              h5(data-aos="fade-up") Zona 3
              p.mt-3.mb-0(data-aos="fade-up") Riesgos poco probables y de bajo impacto.
          .row.m-0.pt-5.pb-4.mb-4.mb-lg-0(style="border-bottom:1px #36257B solid;")
            .col-auto(style="place-self:center")
              figure
                img(src="@/assets/curso/tema3/29.png" data-aos="fade-right")
            .col(style="place-self:center")
              h5(data-aos="fade-up") Zona 4
              p.mt-3.mb-0(data-aos="fade-up") Riesgos improbables, de muy alto impacto.

      p.mt-5.text-center(data-aos="fade-up" style="position:relative") Estas zonas se pueden representar en un mapa de calor, como se muestra en la figura No. 21.

      .col-lg-10.m-auto.mt-5
        .titulo-sexto.color-acento-contenido.mb-0(style="position:relative")
          h5 Figura 21.
          span El riesgo en funcin del impacto y la probabilidad.
        figure
          img.d-none.d-lg-block(src="@/assets/curso/tema3/30.svg" data-aos="zoom-in-up" alt="La imagen presenta el riesgo en funcin del impacto y la probabilidad, donde el eje X es la probabilidad y el eje Y corresponde al impacto, mostrando los 4 principales puntos de relacin.")
          img.d-block.d-lg-none(src="@/assets/curso/tema3/31.svg" data-aos="zoom-in-up" alt="La imagen presenta el riesgo en funcin del impacto y la probabilidad, donde el eje X es la probabilidad y el eje Y corresponde al impacto, mostrando los 4 principales puntos de relacin.")
        figcaption(style="background-color:#F6F6F6;font-weight:400;position:relative") Nota: Recuperado de MAGERIT versin 3.0 Metodologa de Anlisis y Gestin de Riesgos de los Sistemas de Informacin.

    p.mt-5(data-aos="fade-up") A continuacin se va a ampliar la informacin sobre la clasificacin de los riesgos:

    .mt-5.row.mb-5(style="background-color:#40327B;border-top-right-radius:150px;border-bottom-right-radius:150px;max-width:300px;")
      .col-auto.p-0(style="place-self:center;")
        figure
          img(src="@/assets/curso/tema2/2.svg" data-aos="fade-right")
      .col(style="place-self:center;")
        h4.text-white.mb-0(data-aos="fade-up") Riesgo acumulado

    .row
      .col-lg-8.mb-4.mb-lg-0(style="place-self:center")
        p(data-aos="fade-up") Este riesgo se debe calcular en cada activo amenaza y dimensin de seguridad, convirtindose en una funcin del valor acumulado, la degradacin causada y la probabilidad de ocurrencia de la amenaza.
        p.mt-4(data-aos="fade-up") Cuando se calcula sobre los activos base de la informacin, permite identificar las salvaguardas necesarias para aplicar en los entornos de trabajo: endurecimiento de equipos, #[em backup], etc.
      .col-lg-4(style="place-self:center")
        figure
          img(src="@/assets/curso/tema3/32.png" data-aos="fade-left")

    .mt-5.row.mb-5(style="background-color:#40327B;border-top-right-radius:150px;border-bottom-right-radius:150px;max-width:300px;")
      .col-auto.p-0(style="place-self:center;")
        figure
          img(src="@/assets/curso/tema2/2.svg" data-aos="fade-right")
      .col(style="place-self:center;")
        h4.text-white.mb-0(data-aos="fade-up") Riesgo repercutido

    p(data-aos="fade-up") Este riesgo repercutido se obtiene para activo, amenaza y dimensin de valoracin, convirtindose en una funcin del valor propio, la degradacin causada y la probabilidad de la amenaza.
    p.mt-4(data-aos="fade-up") Este riesgo permite determinar las consecuencias de las incidencias tcnicas sobre la finalidad del sistema de informacin, debido a que se consolida como un recurso gerencial que permite la toma de decisiones crticas de un anlisis de riesgos.

    .row.mt-5.p-5.p-xl-0.col-xl-10.m-auto(style="background-color:#D5CEF8;border-radius:30px;")
      .col-xl-4.col-lg-5.ps-0.col-sm-6.col-10.mb-4.mb-lg-0
        figure(style="position:relative")
          img(src="@/assets/curso/tema3/33.png" data-aos="fade-right")
      .col-xl-8.col-lg-7(style="place-self:center;")
        p(data-aos="fade-up") #[strong Agregacin de riesgos:] bajo las siguientes condiciones, se permite la agrupacin de los riesgos:
        ul.lista-ul
          li.mb-0(data-aos="fade-up")
            i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
            span Se puede agregar el riesgo repercutido a diferentes activos.
          li.mb-0(data-aos="fade-up")
            i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
            span Se puede agregar el impacto acumulado sobre activos que no sean dependientes entre s, y no hereden valor de un activo superior comn.
          li.mb-0(data-aos="fade-up")
            i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
            span No debe agregarse el riesgo acumulado sobre activos que no sean independientes, pues ello supondra sobre ponderar el riesgo al incluir varias veces el valor acumulado de activos superiores.
          li.mb-0(data-aos="fade-up")
            i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
            span Puede agregarse el riesgo de diferentes amenazas sobre un mismo activo, aunque conviene considerar en qu medida las diferentes amenazas son independientes y pueden ser concurrentes.
          li.mb-0(data-aos="fade-up")
            i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
            span Se puede agregar el riesgo de una amenaza en diferentes dimensiones.

    Separador.mt-5

    .titulo-segundo
      h2#t_3_5 3.5 Establecimiento de salvaguardas

    p(data-aos="fade-up") A partir de este punto se proceden a determinar los controles que sern necesarios para reducir el riesgo y las amenazas; algunas amenazas pueden ser controladas a partir de cambios y acciones de gestin sobre algn activo, pero en otras ocasiones, estas debern de intervenir a partir de controles tcnicos tecnolgicos o procedimentales.
    p.mt-4(data-aos="fade-up") A continuacin, se describen las acciones que se deben aplicar para reducir el riesgo informtico:

    figure
      img.d-none.d-lg-block(src="@/assets/curso/tema3/34a.svg" data-aos="zoom-in-up")
      img.d-block.d-lg-none(src="@/assets/curso/tema3/34c.svg" data-aos="zoom-in-up")

    .cont_3_4.mt-5(style="position:relative")
      .col-xl-10.col-lg-12.col-md-12.m-auto
        p(data-aos="fade-up" style="position:relative") #[strong Tipo de proteccin:] la determinacin del tipo de proteccin es fundamental para identificar el tipo de proteccin, a continuacin, en la tabla No. 4 vamos a reconocer los tipos sugeridos por la metodologa Magerit.
        .titulo-sexto.color-acento-contenido.mt-5(style="position:relative")
          h5 Tabla 4.
          span Tipos de proteccin sugeridos por Magerit.

        .tabla-a.color-acento-botones.mt-5(style="position:relative")
          table
            caption(style="background-color:#F6F6F6;font-weight:400;") Nota. Recuperado de MAGERIT versin 3.0 Metodologa de Anlisis y Gestin de Riesgos de los Sistemas de Informacin https://www.ccn-cert.cni.es/documentos-publicos/1789-magerit-libro-i-metodo/file.html
            thead(style="border:0;")
              tr(style="background-color:#36257B;")
                th.p-4.text-white.text-center(style="border:0;font-size:17px; width: 30%") Tipo Proteccin
                th.p-4.text-white.text-center(style="border:0;font-size:17px;") Descripcin
            tbody
              tr(style="background-color:#fff;")
                td.p-4(style="border:0;font-weight:bold;border-right:1px #AFAFAF solid;") [PR] Prevencin
                td.p-4(style="border:0")
                  p Salvaguardas preventivas que reducen las oportunidades de que un incidente ocurra. Si la salvaguarda falla y el incidente llega a ocurrir, los daos son los mismos.
                  p.mt-4 #[strong Ejemplos:] autorizacin previa de los usuarios, gestin de privilegios, planificacin de capacidades, metodologa segura de desarrollo de #[em software], pruebas en preproduccin, segregacin de tareas.
              tr(style="background-color:#D1F1F6;")
                td.p-4(style="border:0;font-weight:bold;border-right:1px #AFAFAF solid;") [DR] Disuasin
                td.p-4(style="border:0;")
                  p Salvaguarda disuasoria, tiene efecto sobre los atacantes, reduciendo la intencin de que estos se atrevan a atacar un activo.
                  p.mt-4 #[strong Ejemplos:] vallas elevadas, guardias de seguridad, avisos sobre la persecucin del delito o persecucin del delincuente.
              tr(style="background-color:#fff;")
                td.p-4(style="border:0;font-weight:bold;border-right:1px #AFAFAF solid;") [EL] Eliminacin
                td.p-4(style="border:0")
                  p Son salvaguardas que eliminan un incidente, impidiendo que tenga lugar, lo que significa que actan antes que el incidente se haya producido. No reducen los daos en caso de que la salvaguarda no sea perfecta y el incidente llegue a ocurrir.
                  p.mt-4 #[strong Ejemplos:] eliminacin de cuentas estndar o sin contrasea y de servicios innecesarios, y en general, todo lo que tenga que ver con la fortificacin o bastionado, cifrado de informacin y armarios ignfugos.
              tr(style="background-color:#D1F1F6;")
                td.p-4(style="border:0;font-weight:bold;border-right:1px #AFAFAF solid;") [IM] Minimizacin del impacto / limitacin del impacto
                td.p-4(style="border:0;")
                  p Son salvaguardas que minimizan o limitan el impacto, acotando las consecuencias de un incidente.
                  p.mt-4 #[strong Ejemplos:] desconexin de redes o equipos o detencin de servicios en caso de ataque, seguros de cobertura, cumplimiento de la legislacin vigente.
              tr(style="background-color:#fff;")
                td.p-4(style="border:0;font-weight:bold;border-right:1px #AFAFAF solid;") [CR] Correccin
                td.p-4(style="border:0")
                  p Son salvaguardas que actan despus de un incidente, ejerciendo una reparacin al activo.
                  p.mt-4 #[strong Ejemplos:] gestin de incidentes, lneas de comunicacin alternativas, fuentes de alimentacin redundantes.
              tr(style="background-color:#D1F1F6;")
                td.p-4(style="border:0;font-weight:bold;border-right:1px #AFAFAF solid;") [RC] Recuperacin
                td.p-4(style="border:0;")
                  p Son salvaguardas que ofrecen recuperacin a un activo que ha sufrido una alteracin, regresando al estado til y viable.
                  p.mt-4 #[strong Ejemplos:] copias de seguridad (#[em back-up]).
              tr(style="background-color:#fff;")
                td.p-4(style="border:0;font-weight:bold;border-right:1px #AFAFAF solid;") [MN] Monitorizacin
                td.p-4(style="border:0")
                  p Son salvaguardas enfocadas en la vigilancia y el monitoreo de activos, para identificar posibles cambios o alteraciones en el normal comportamiento de un activo de informacin.
                  p.mt-4 #[strong Ejemplos:] registros de actividad, registro de descargas de web.
              tr(style="background-color:#D1F1F6;")
                td.p-4(style="border:0;font-weight:bold;border-right:1px #AFAFAF solid;") [DC] Deteccin
                td.p-4(style="border:0;")
                  p Son salvaguardas que detectan un ataque, determinando as lo que est sucediendo. No necesariamente deben detener la accin, pero s permiten establecer las medidas mnimas necesarias para su proteccin.
                  p.mt-4 #[strong Ejemplos:] antivirus, IDS, detectores de incendio.
              tr(style="background-color:#fff;")
                td.p-4(style="border:0;font-weight:bold;border-right:1px #AFAFAF solid;") [AW] Concienciacin
                td.p-4(style="border:0")
                  p Son actividades relacionadas con la transferencia del conocimiento para la seguridad de todos los actores involucrados en la organizacin, y que dependen de los activos de informacin.
                  p.mt-4 #[strong Ejemplos:] cursos de concienciacin, cursos de formacin.
              tr(style="background-color:#D1F1F6;")
                td.p-4(style="border:0;font-weight:bold;border-right:1px #AFAFAF solid;") [AD] Administracin
                td.p-4(style="border:0;")
                  p Son salvaguardas relacionadas con los componentes de seguridad del sistema.
                  p.mt-4 #[strong Ejemplos:] inventario de activos, anlisis de riesgos, plan de continuidad.

        p.my-5(data-aos="fade-up" style="position:relative") De acuerdo al modelo anterior, podremos determinar y agrupar las salvaguardas de acuerdo a su efecto sobre una amenaza, como se puede identificar en la tabla No. 5.

        .titulo-sexto.color-acento-contenido.mt-5(style="position:relative")
          h5 Tabla 5.
          span Tipos de salvaguardas sugeridos por Magerit.

        .tabla-a.color-acento-botones.mt-5(style="position:relative")
          table
            caption(style="background-color:#F6F6F6;font-weight:400;") Nota. https://cutt.ly/DB7caei
            thead(style="border:0;")
              tr(style="background-color:#36257B;")
                th.p-4.text-white.text-center(style="border:0;font-size:17px;") Efecto
                th.p-4.text-white.text-center(style="border:0;font-size:17px;") A diario
            tbody
              tr(style="background-color:#D1F1F6;")
                td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;") Preventivas: reducen la probabilidad
                td.p-4.text-center(style="border:0") [PR] preventivas #[br] [DR] disuasorias #[br] [EL] eliminatorias
              tr(style="background-color:#fff;")
                td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;") Acotan la degradacin
                td.p-4.text-center(style="border:0;") [IM] minimizadoras #[br] [CR] correctivas #[br] [RC] recuperativas
              tr(style="background-color:#D1F1F6;")
                td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;") Consolidan el efecto de las dems
                td.p-4.text-center(style="border:0") [MN] de monitorizacin #[br] [DC] de deteccin #[br] [AW] de concienciacin #[br] [AD] administrativas

    .col-xl-10.col-lg-12.col-md-12.m-auto.mt-5
      p(data-aos="fade-up") Las salvaguardas tambin por la eficacia en el momento de actuar frente al riesgo para el cual fueron consideradas, una salvaguarda adecuada en 100 % eficaz si combina los siguientes factores:
      .row.mt-4.pt-4.pb-0.pe-5(style="background-color:#D5CEF8;border-radius:30px;")
        .col-xl-5.col-lg-6.ps-0
          figure(style="position:relative")
            img(src="@/assets/curso/tema3/37.png" data-aos="fade-right")
        .col-xl-7.col-lg-6.p-5.p-xl-0(style="place-self:center;")
          p(data-aos="fade-up") #[strong Desde el punto de vista tcnico.]
          ul.lista-ul.mt-2
            li.mb-0(data-aos="fade-up")
              i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
              | Tcnicamente adecuada para enfrentarse al riesgo que protege.
            li.mb-0(data-aos="fade-up")
              i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
              | Aplicacin permanente.
          p.mt-3(data-aos="fade-up") #[strong Desde el punto de vista de operacin de la salvaguarda.]
          ul.lista-ul.mt-2
            li.mb-0(data-aos="fade-up")
              i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
              | Perfectamente desplegada, configurada y mantenida.
            li.mb-0(data-aos="fade-up")
              i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
              | Existen procedimientos claros de uso normal y en caso de incidencias.
            li.mb-0(data-aos="fade-up")
              i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
              | Los usuarios estn formados y concienciados.
            li.mb-0(data-aos="fade-up")
              i.fas.fa-circle(style="color:#1DBBD2;font-size:6px;")
              | Existen controles que avisan de posibles fallos.

      p.mt-5(data-aos="fade-up") Entre una eficacia del 0 % para aquellas que faltan y el 100 % para aquellas que son idneas y que estn perfectamente implantadas, se estimar un grado de eficacia real en cada caso concreto. Para medir los aspectos organizativos, se puede emplear una escala de madurez que recoja en forma de factor corrector la confianza que merece el proceso de gestin de la salvaguarda:

    .col-xl-10.col-lg-12.col-md-12.m-auto.mt-5
      .titulo-sexto.color-acento-contenido(style="position:relative")
        h5 Tabla 6.
        span Eficacia y madurez de las salvaguardas.

      .tabla-a.color-acento-botones.mt-5(style="position:relative")
        table
          caption(style="background-color:#F6F6F6;font-weight:400;") Nota. https://cutt.ly/DB7caei
          thead(style="border:0;")
            tr(style="background-color:#36257B;")
              th.p-4.text-white.text-center(style="border:0;font-size:17px; width: 120px") Factor
              th.p-4.text-white.text-center(style="border:0;font-size:17px; width: 120px") Nivel
              th.p-4.text-white.text-center(style="border:0;font-size:17px;") Significado
          tbody
            tr(style="background-color:#D1F1F6;")
              td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;") 0%
              td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;") L0
              td.p-4.text-center(style="border:0") Inexistente
            tr(style="background-color:#fff;")
              td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;")
              td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;") L1
              td.p-4.text-center(style="border:0;") Inicial / ad hoc
            tr(style="background-color:#D1F1F6;")
              td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;")
              td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;") L2
              td.p-4.text-center(style="border:0") Reproducible, pero intuitivo
            tr(style="background-color:#fff;")
              td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;")
              td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;") L3
              td.p-4.text-center(style="border:0;") Proceso definido
            tr(style="background-color:#D1F1F6;")
              td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;")
              td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;") L4
              td.p-4.text-center(style="border:0") Gestionado y medible
            tr(style="background-color:#fff;")
              td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;") 100%
              td.p-4.text-center(style="border:0;border-right:1px #AFAFAF solid;") L5
              td.p-4.text-center(style="border:0;") Optimizado

    Separador.mt-5

    .titulo-segundo
      h2#t_3_6 3.6 Impacto residual

    p(data-aos="fade-up") De acuerdo al conjunto de salvaguardas determinadas y desplegadas y una medida de la madurez de su proceso de gestin, el sistema queda en una situacin de posible impacto que debe ser mnimo, al cual se le denomina residual. Y se consolida una vez hayamos modificado el impacto, desde un valor potencial a un valor residual.

    .row.mt-5.col-lg-10.m-auto.p-3.pe-0(style="background-color:#D1F1F6;border-radius:20px;")
      .col-lg-10.col-md-8.mb-4.mb-md-0(style="place-self:center")
        p.mb-0(data-aos="fade-up") Su clculo es determinado a partir de la premisa de que un activo no ha sufrido cambio ni degradacin, y que las salvaguardas implementadas han actuado de manera positiva evitando la consolidacin de algn tipo de incidente.
      .col-lg-2.col-md-4.pe-0(style="place-self:center")
        figure.figura_3_1
          img(src="@/assets/curso/tema3/38.svg" data-aos="fade-left" style="position:relative;right:-50%;max-width:100px;")

    p.mt-5.text-center(data-aos="fade-up") El impacto residual puede calcularse a partir de los activos inferiores, o repercutido sobre los activos superiores.

    .row.mt-4.pt-4.pb-0.pe-5(style="background-color:#EAE6FB;")
      .col-xl-4.col-lg-5.ps-0.col-sm-7.col-8
        figure(style="position:relative")
          img(src="@/assets/curso/tema3/39.png" data-aos="fade-right").d-none.d-lg-block
      .col-xl-8.col-lg-7(style="place-self:center;")
        p(data-aos="fade-up") #[strong Riesgo residual]
        p.mt-4(data-aos="fade-up") Este riesgo residual, es calculado a partir del conjunto de salvaguardas implementadas y que conllevan a que un activo no est sujeto a una potencial alteracin en su calidad por ende no ha sido degradado.
        p.mt-4(data-aos="fade-up") El clculo del riesgo residual se determina de la siguiente manera:
        ul.lista-ul.mt-4
          li.mb-2(data-aos="fade-up")
